import React from 'react';
import { Link } from 'react-router-dom';
import { Routes } from '../../utils/consts';

import './main-navigation.scss';

const MainNavigation = () => {
    return (
        <ul className="main-navigation">
            <li>
                <Link to={Routes.HOME_ROUTE}>  </Link>
            </li>
            <li>
                <Link to={Routes.ABOUT_ROUTE}>   </Link>
            </li>
            <li>
                <Link to={Routes.CONTACTS_ROUTE}>  </Link>
            </li>
            <li>
                <Link to={Routes.SHOP_ROUTE}>  </Link>
            </li>
            <li>
                <Link to={Routes.PAYMENT_DELIVERY_ROUTE}>    </Link>
            </li>
        </ul>
    )
}

export default MainNavigation
